import React, { useState } from 'react';
import { useParams, Link } from 'react-router-dom';
import { motion } from 'framer-motion';
import { Calendar, MapPin, Users, ChevronLeft, Loader } from 'lucide-react';
import { mockEvents } from '../data/siteData';  

// 模擬檢查學生會費的函數
const checkStudentFee = (studentId, name) => {
  return new Promise((resolve) => {
    setTimeout(() => {
      // 模擬根據學號的最後一位數字來決定是否繳費
      const hasPaid = parseInt(studentId.slice(-1)) % 2 === 0;
      resolve(hasPaid);
    }, 1000); // 模擬網絡請求延遲
  });
};

const EventDetailPage = () => {
  const { id } = useParams();
  const event = mockEvents.find(e => e.id === id) || mockEvents[0];
  const [formData, setFormData] = useState({
    name: '',
    studentId: '',
    email: '',
    department: '',
    yearOfStudy: ''
  });
  const [isChecking, setIsChecking] = useState(false);
  const [hasPaidFee, setHasPaidFee] = useState(null);
  const [isRegistered, setIsRegistered] = useState(false);

  const handleInputChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleCheckFee = async (e) => {
    e.preventDefault();
    setIsChecking(true);
    const result = await checkStudentFee(formData.studentId, formData.name);
    setHasPaidFee(result);
    setIsChecking(false);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    // 這裡應該有處理表單提交的邏輯
    setIsRegistered(true);
  };

  const renderRegistrationForm = () => (
    <form onSubmit={handleCheckFee} className="space-y-4">
      <div>
        <label htmlFor="name" className="block text-sm font-medium text-gray-700">姓名</label>
        <input 
          type="text" 
          id="name" 
          name="name" 
          value={formData.name} 
          onChange={handleInputChange} 
          required 
          className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-purple-500 focus:ring-purple-500"
        />
      </div>
      <div>
        <label htmlFor="studentId" className="block text-sm font-medium text-gray-700">學號</label>
        <input 
          type="text" 
          id="studentId" 
          name="studentId" 
          value={formData.studentId} 
          onChange={handleInputChange} 
          required 
          className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-purple-500 focus:ring-purple-500"
        />
      </div>
      <div>
        <label htmlFor="email" className="block text-sm font-medium text-gray-700">電子郵件</label>
        <input 
          type="email" 
          id="email" 
          name="email" 
          value={formData.email} 
          onChange={handleInputChange} 
          required 
          className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-purple-500 focus:ring-purple-500"
        />
      </div>
      <div>
        <label htmlFor="department" className="block text-sm font-medium text-gray-700">科系</label>
        <input 
          type="text" 
          id="department" 
          name="department" 
          value={formData.department} 
          onChange={handleInputChange} 
          required 
          className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-purple-500 focus:ring-purple-500"
        />
      </div>
      <div>
        <label htmlFor="yearOfStudy" className="block text-sm font-medium text-gray-700">年級</label>
        <select 
          id="yearOfStudy" 
          name="yearOfStudy" 
          value={formData.yearOfStudy} 
          onChange={handleInputChange} 
          required 
          className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-purple-500 focus:ring-purple-500"
        >
          <option value="">請選擇</option>
          <option value="1">大一</option>
          <option value="2">大二</option>
          <option value="3">大三</option>
          <option value="4">大四</option>
          <option value="5">研究生</option>
        </select>
      </div>
      <button 
        type="submit" 
        className="w-full bg-purple-600 text-white py-2 px-4 rounded-md hover:bg-purple-700 transition duration-300"
        disabled={isChecking}
      >
        {isChecking ? <Loader className="animate-spin mx-auto" /> : '檢查學生會費狀態'}
      </button>
    </form>
  );

  const renderPaymentOptions = () => (
    <div className="space-y-4">
      <p className="text-lg font-semibold">
        {hasPaidFee 
          ? '您已繳納學生會費，可享受優惠價格！' 
          : '您尚未繳納學生會費，請選擇以下選項：'}
      </p>
      <form onSubmit={handleSubmit}>
        {hasPaidFee ? (
          <button 
            type="submit" 
            className="w-full bg-green-500 text-white py-2 px-4 rounded-md hover:bg-green-600 transition duration-300"
          >
            免費報名參加
          </button>
        ) : (
          <>
            <button 
              type="submit" 
              className="w-full bg-yellow-500 text-white py-2 px-4 rounded-md hover:bg-yellow-600 transition duration-300 mb-2"
            >
              繳納學生會費並報名（NT$500）
            </button>
            <button 
              type="submit" 
              className="w-full bg-red-500 text-white py-2 px-4 rounded-md hover:bg-red-600 transition duration-300"
            >
              直接報名參加（NT$200）
            </button>
          </>
        )}
      </form>
    </div>
  );

  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
      className="bg-gray-100 min-h-screen py-12"
    >
      <div className="container mx-auto px-4">
        <Link to="/events" className="inline-flex items-center text-purple-600 hover:text-purple-800 transition duration-300 mb-6">
          <ChevronLeft size={16} className="mr-1" />
          返回活動列表
        </Link>
        
        <div className="bg-white rounded-lg shadow-xl overflow-hidden">
          <img src={event.image} alt={event.title} className="w-full h-64 object-cover" />
          <div className="p-6">
            <h1 className="text-3xl font-bold text-gray-800 mb-4">{event.title}</h1>
            <p className="text-gray-600 mb-6">{event.description}</p>
            
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
              <div className="flex items-center text-gray-500">
                <Calendar size={20} className="mr-2" />
                <span>{event.date}</span>
              </div>
              <div className="flex items-center text-gray-500">
                <MapPin size={20} className="mr-2" />
                <span>{event.location}</span>
              </div>
              <div className="flex items-center text-gray-500">
                <Users size={20} className="mr-2" />
                <span>{event.capacity} 名額</span>
              </div>
            </div>

            {isRegistered ? (
              <div className="bg-green-100 border-l-4 border-green-500 text-green-700 p-4" role="alert">
                <p className="font-bold">報名成功！</p>
                <p>感謝您報名參加 {event.title}。我們將通過電子郵件發送詳細信息。</p>
              </div>
            ) : hasPaidFee === null ? (
              renderRegistrationForm()
            ) : (
              renderPaymentOptions()
            )}
          </div>
        </div>
      </div>
    </motion.div>
  );
};

export default EventDetailPage;