import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { MessageSquare, AlertTriangle, Star, Send, X, Mail, Phone, MapPin } from 'lucide-react';

const ContactPage = ({ theme }) => {
  const [activeSection, setActiveSection] = useState(null);
  const [formData, setFormData] = useState({
    name: '', email: '', content: ''
  });
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitSuccess, setSubmitSuccess] = useState(false);

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setIsSubmitting(true);
    setTimeout(() => {
      setIsSubmitting(false);
      setSubmitSuccess(true);
      setFormData({ name: '', email: '', content: '' });
      setTimeout(() => {
        setSubmitSuccess(false);
        setActiveSection(null);
      }, 3000);
    }, 2000);
  };

  const sections = [
    {
      id: 'suggestion',
      title: '建議箱',
      icon: MessageSquare,
      color: 'from-blue-400 to-blue-600',
      hoverColor: 'from-blue-500 to-blue-700',
    },
    {
      id: 'complaint',
      title: '投訴',
      icon: AlertTriangle,
      color: 'from-red-400 to-red-600',
      hoverColor: 'from-red-500 to-red-700',
    },
    {
      id: 'wish',
      title: '許願箱',
      icon: Star,
      color: 'from-yellow-400 to-yellow-600',
      hoverColor: 'from-yellow-500 to-yellow-700',
    },
  ];

  return (
    <div className={`min-h-screen py-12 px-4 ${theme === 'dark' ? 'bg-gray-900 text-white' : 'bg-purple-50 text-gray-900'}`}>
      <motion.h1 
        className={`text-4xl md:text-5xl font-bold text-center mb-12 ${theme === 'dark' ? 'text-purple-300' : 'text-purple-800'}`}
        initial={{ y: -50, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{ type: "spring", stiffness: 100 }}
      >
        聯絡我們
      </motion.h1>

      <div className="max-w-6xl mx-auto">
        <div className="grid grid-cols-1 md:grid-cols-3 gap-8 mb-12">
          {sections.map((section) => (
            <motion.div
              key={section.id}
              className={`relative overflow-hidden rounded-lg shadow-lg cursor-pointer
                          ${theme === 'dark' ? 'bg-gray-800' : 'bg-white'}`}
              whileHover={{ scale: 1.05 }}
              onClick={() => setActiveSection(section.id)}
            >
              <div className={`absolute inset-0 bg-gradient-to-br ${section.color} opacity-75`}></div>
              <div className="relative p-6 flex flex-col items-center justify-center h-full">
                <motion.div
                  animate={{
                    y: [0, -10, 0],
                    scale: [1, 1.2, 1],
                    rotate: section.id === 'wish' ? [0, 360] : 0,
                  }}
                  transition={{
                    duration: 2,
                    repeat: Infinity,
                    repeatType: "reverse",
                  }}
                >
                  <section.icon size={48} className="text-white mb-4" />
                </motion.div>
                <h2 className="text-2xl font-bold text-white">{section.title}</h2>
              </div>
            </motion.div>
          ))}
        </div>

        <AnimatePresence>
          {activeSection && (
            <motion.div
              initial={{ opacity: 0, scale: 0.8 }}
              animate={{ opacity: 1, scale: 1 }}
              exit={{ opacity: 0, scale: 0.8 }}
              className={`fixed inset-0 z-50 flex items-center justify-center p-4`}
            >
              <div className="absolute inset-0 bg-black opacity-50" onClick={() => setActiveSection(null)}></div>
              <div className={`relative ${theme === 'dark' ? 'bg-gray-800' : 'bg-white'} rounded-lg shadow-2xl p-8 max-w-md w-full`}>
                <button 
                  onClick={() => setActiveSection(null)}
                  className={`absolute top-4 right-4 ${theme === 'dark' ? 'text-gray-400 hover:text-gray-200' : 'text-gray-600 hover:text-gray-800'}`}
                >
                  <X size={24} />
                </button>
                <h2 className={`text-3xl font-bold mb-6 ${theme === 'dark' ? 'text-purple-300' : 'text-purple-700'}`}>
                  {sections.find(s => s.id === activeSection).title}
                </h2>
                <form onSubmit={handleSubmit} className="space-y-6">
                  <div>
                    <label className={`block font-medium mb-2 ${theme === 'dark' ? 'text-gray-200' : 'text-gray-700'}`} htmlFor="name">姓名</label>
                    <input
                      type="text"
                      id="name"
                      name="name"
                      value={formData.name}
                      onChange={handleChange}
                      className={`w-full p-3 border rounded-lg focus:outline-none focus:ring-2 focus:ring-purple-500 ${
                        theme === 'dark' ? 'bg-gray-700 border-gray-600 text-white' : 'bg-white border-gray-300'
                      }`}
                      required
                    />
                  </div>
                  <div>
                    <label className={`block font-medium mb-2 ${theme === 'dark' ? 'text-gray-200' : 'text-gray-700'}`} htmlFor="email">電子郵件</label>
                    <input
                      type="email"
                      id="email"
                      name="email"
                      value={formData.email}
                      onChange={handleChange}
                      className={`w-full p-3 border rounded-lg focus:outline-none focus:ring-2 focus:ring-purple-500 ${
                        theme === 'dark' ? 'bg-gray-700 border-gray-600 text-white' : 'bg-white border-gray-300'
                      }`}
                      required
                    />
                  </div>
                  <div>
                    <label className={`block font-medium mb-2 ${theme === 'dark' ? 'text-gray-200' : 'text-gray-700'}`} htmlFor="content">
                      {activeSection === 'suggestion' ? '您的建議' : 
                       activeSection === 'complaint' ? '投訴內容' : '您的願望'}
                    </label>
                    <textarea
                      id="content"
                      name="content"
                      value={formData.content}
                      onChange={handleChange}
                      rows="4"
                      className={`w-full p-3 border rounded-lg focus:outline-none focus:ring-2 focus:ring-purple-500 ${
                        theme === 'dark' ? 'bg-gray-700 border-gray-600 text-white' : 'bg-white border-gray-300'
                      }`}
                      required
                    ></textarea>
                  </div>
                  <div className="flex justify-end">
                    <motion.button
                      type="submit"
                      whileHover={{ scale: 1.05 }}
                      whileTap={{ scale: 0.95 }}
                      disabled={isSubmitting}
                      className={`bg-purple-600 text-white px-6 py-3 rounded-lg font-semibold flex items-center ${
                        isSubmitting ? 'opacity-50 cursor-not-allowed' : 'hover:bg-purple-700'
                      }`}
                    >
                      {isSubmitting ? '提交中...' : '提交'} <Send size={16} className="ml-2" />
                    </motion.button>
                  </div>
                </form>
              </div>
            </motion.div>
          )}
        </AnimatePresence>

        <AnimatePresence>
          {submitSuccess && (
            <motion.div
              initial={{ opacity: 0, y: 50 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: 50 }}
              className={`fixed inset-0 z-50 flex items-center justify-center p-4`}
            >
              <div className="absolute inset-0 bg-black opacity-50"></div>
              <div className={`relative ${theme === 'dark' ? 'bg-gray-800' : 'bg-white'} rounded-lg shadow-2xl p-8 max-w-md w-full text-center`}>
                <motion.div
                  animate={{
                    scale: [1, 1.2, 1],
                    rotate: [0, 360, 0],
                  }}
                  transition={{ duration: 1 }}
                  className={`text-6xl mb-4 ${activeSection === 'suggestion' ? 'text-blue-500' : 
                                              activeSection === 'complaint' ? 'text-red-500' : 
                                              'text-yellow-500'}`}
                >
                  {activeSection === 'suggestion' ? '💡' : 
                   activeSection === 'complaint' ? '🔔' : 
                   '✨'}
                </motion.div>
                <h3 className={`text-2xl font-bold mb-4 ${theme === 'dark' ? 'text-white' : 'text-gray-800'}`}>
                  {activeSection === 'suggestion' ? '感謝您的建議！' : 
                   activeSection === 'complaint' ? '我們已收到您的投訴' : 
                   '您的願望已被聽見'}
                </h3>
                <p className={`${theme === 'dark' ? 'text-gray-300' : 'text-gray-600'}`}>
                  {activeSection === 'suggestion' ? '我們會認真考慮您的意見，共同創造更美好的未來。' : 
                   activeSection === 'complaint' ? '我們會盡快處理您的投訴，並採取必要的行動。' : 
                   '我們會努力實現您的願望，讓世界變得更美好。'}
                </p>
              </div>
            </motion.div>
          )}
        </AnimatePresence>

        <motion.div
          className={`${theme === 'dark' ? 'bg-gray-800' : 'bg-white'} rounded-lg p-6 shadow-md`}
          initial={{ opacity: 0, y: 50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.4 }}
        >
          <h2 className={`text-2xl font-bold mb-4 ${theme === 'dark' ? 'text-purple-300' : 'text-purple-800'}`}>聯絡資訊</h2>
          <div className="space-y-3">
            <div className="flex items-center">
              <Phone className={`mr-2 ${theme === 'dark' ? 'text-purple-400' : 'text-purple-600'}`} />
              <p className={theme === 'dark' ? 'text-gray-300' : 'text-gray-700'}>(02) 1234-5678</p>
            </div>
            <div className="flex items-center">
              <Mail className={`mr-2 ${theme === 'dark' ? 'text-purple-400' : 'text-purple-600'}`} />
              <p className={theme === 'dark' ? 'text-gray-300' : 'text-gray-700'}>contact@genderhumanrights.tw</p>
            </div>
            <div className="flex items-center">
              <MapPin className={`mr-2 ${theme === 'dark' ? 'text-purple-400' : 'text-purple-600'}`} />
              <p className={theme === 'dark' ? 'text-gray-300' : 'text-gray-700'}>台北市中正區重慶南路一段2號</p>
            </div>
          </div>
        </motion.div>
      </div>
    </div>
  );
};

export default ContactPage;