// src/pages/ReportsPage.js
import React from 'react';
import { motion } from 'framer-motion';
import { Link } from 'react-router-dom';
import { ChevronRight } from 'lucide-react';
import { researchReports, events, resources } from '../data/siteData';


const ReportsPage = () => {
  return (
    <motion.div 
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
      className="bg-gray-100 min-h-screen py-12"
    >
      <div className="container mx-auto px-4">
        <h1 className="text-4xl font-bold text-center text-gray-800 mb-8">研究報告</h1>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
          {researchReports.map((report, index) => (
            <motion.div
              key={report.id}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: index * 0.1 }}
              className="bg-white rounded-lg shadow-xl overflow-hidden"
            >
              <img src={report.image} alt={report.title} className="w-full h-48 object-cover" />
              <div className="p-6">
                <h2 className="text-2xl font-semibold text-gray-800 mb-2">{report.title}</h2>
                <p className="text-gray-600 mb-4">{report.excerpt}</p>
                <Link to={`/reports/${report.id}`} className="inline-flex items-center text-purple-600 hover:text-purple-800 transition duration-300">
                  閱讀全文 <ChevronRight size={16} className="ml-1" />
                </Link>
              </div>
            </motion.div>
          ))}
        </div>
      </div>
    </motion.div>
  );
};

export default ReportsPage;