import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { motion, AnimatePresence } from 'framer-motion';
import { Sun, Moon, Menu, X, Search } from 'lucide-react';

const Navbar = ({ theme, toggleTheme, setShowSearch }) => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  const handleSearchClick = () => {
    if (typeof setShowSearch === 'function') {
      setShowSearch(true);
    } else {
      console.warn('setShowSearch is not a function. Search functionality is disabled.');
    }
  };

  return (
    <nav className={`fixed top-0 left-0 right-0 z-50 ${theme === 'dark' ? 'bg-gray-800 text-white' : 'bg-white text-gray-900'} shadow-md`}>
      <div className="container mx-auto px-4 py-4 flex justify-between items-center">
        <Link to="/" className="text-2xl font-bold">性別與人權部</Link>
        <div className="hidden md:flex space-x-6">
          <NavLink to="/">首頁</NavLink>
          <NavLink to="/about">關於我們</NavLink>
          <NavLink to="/reports">研究報告</NavLink>
          <NavLink to="/events">活動</NavLink>
          <NavLink to="/resources">資源</NavLink>
          <NavLink to="/contact">聯絡我們</NavLink>
        </div>
        <div className="flex items-center space-x-4">
          <button onClick={handleSearchClick} className="p-2 rounded-full hover:bg-gray-200 dark:hover:bg-gray-700">
            <Search size={20} />
          </button>
          <button onClick={toggleTheme} className="p-2 rounded-full hover:bg-gray-200 dark:hover:bg-gray-700">
            {theme === 'light' ? <Moon size={20} /> : <Sun size={20} />}
          </button>
          <button className="md:hidden" onClick={toggleMenu}>
            <Menu size={24} />
          </button>
        </div>
      </div>
      <AnimatePresence>
        {isMenuOpen && <MobileMenu theme={theme} toggleMenu={toggleMenu} />}
      </AnimatePresence>
    </nav>
  );
};

const NavLink = ({ to, children }) => (
  <Link 
    to={to} 
    className="hover:text-purple-600 transition duration-300"
  >
    {children}
  </Link>
);

const MobileMenu = ({ theme, toggleMenu }) => (
  <motion.div
    initial={{ x: '100%' }}
    animate={{ x: 0 }}
    exit={{ x: '100%' }}
    transition={{ type: 'tween' }}
    className={`fixed inset-y-0 right-0 w-64 ${theme === 'dark' ? 'bg-gray-800' : 'bg-white'} shadow-lg z-50 p-4`}
  >
    <button onClick={toggleMenu} className="absolute top-4 right-4">
      <X size={24} />
    </button>
    <div className="flex flex-col space-y-4 mt-12">
      <NavLink to="/">首頁</NavLink>
      <NavLink to="/about">關於我們</NavLink>
      <NavLink to="/reports">研究報告</NavLink>
      <NavLink to="/events">活動</NavLink>
      <NavLink to="/resources">資源</NavLink>
      <NavLink to="/contact">聯絡我們</NavLink>
    </div>
  </motion.div>
);

export default Navbar;