import React from 'react';
import { motion } from 'framer-motion';
import { ArrowLeft, Calendar, Download, ExternalLink } from 'lucide-react';
import { Link, useParams } from 'react-router-dom';
import { researchReports } from '../data/siteData';

const ResearchReportDetailPage = () => {
  const { id } = useParams();
  const report = researchReports.find(r => r.id === id);

  if (!report) {
    return <div>報告未找到</div>;
  }

  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
      className="bg-gray-100 min-h-screen py-12"
    >
      <div className="container mx-auto px-4">
        <Link to="/reports" className="inline-flex items-center text-purple-600 hover:text-purple-800 mb-6">
          <ArrowLeft size={16} className="mr-2" />
          返回研究報告列表
        </Link>
        <div className="bg-white rounded-lg shadow-xl overflow-hidden">
          <div className="p-6">
            <h1 className="text-3xl font-bold text-gray-800 mb-4">{report.title}</h1>
            <div className="flex items-center text-gray-500 mb-4">
              <Calendar size={16} className="mr-2" />
              <span>{report.date}</span>
            </div>
            <p className="text-gray-600 mb-6">{report.description}</p>
            <div className="space-y-4">
              <h2 className="text-xl font-semibold text-gray-800">主要發現</h2>
              <ul className="list-disc pl-5 space-y-2">
                {report.findings.map((finding, index) => (
                  <li key={index} className="text-gray-600">{finding}</li>
                ))}
              </ul>
            </div>
            <div className="mt-8 space-y-4">
              <a
                href={report.downloadUrl}
                className="inline-flex items-center bg-purple-600 text-white px-4 py-2 rounded hover:bg-purple-700 transition duration-300"
              >
                <Download size={16} className="mr-2" />
                下載完整報告
              </a>
              {report.externalLink && (
                <a
                  href={report.externalLink}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="inline-flex items-center text-purple-600 hover:text-purple-800 ml-4"
                >
                  <ExternalLink size={16} className="mr-2" />
                  查看相關資源
                </a>
              )}
            </div>
          </div>
        </div>
      </div>
    </motion.div>
  );
};

export default ResearchReportDetailPage;