// BudgetPage.js
import React from 'react';
import { motion } from 'framer-motion';
import { Pie, Bar } from 'react-chartjs-2';
import { Chart as ChartJS, ArcElement, Tooltip, Legend, CategoryScale, LinearScale, BarElement, Title } from 'chart.js';

ChartJS.register(ArcElement, Tooltip, Legend, CategoryScale, LinearScale, BarElement, Title);

const BudgetPage = () => {
  const pieData = {
    labels: ['研究經費', '活動經費', '行政開支', '設備採購'],
    datasets: [
      {
        data: [40, 30, 20, 10],
        backgroundColor: ['#8B5CF6', '#EC4899', '#10B981', '#F59E0B'],
      },
    ],
  };

  const barData = {
    labels: ['第一季', '第二季', '第三季', '第四季'],
    datasets: [
      {
        label: '預算使用情況',
        data: [12000, 19000, 15000, 18000],
        backgroundColor: '#8B5CF6',
      },
    ],
  };

  return (
    <div className="bg-gradient-to-b from-purple-100 to-white min-h-screen py-12">
      <div className="container mx-auto px-4">
        <motion.h1 
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          className="text-4xl font-bold text-center text-purple-800 mb-12"
        >
          預算公開
        </motion.h1>
        
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
          <motion.div
            initial={{ opacity: 0, x: -50 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.5, delay: 0.2 }}
            className="bg-white rounded-lg shadow-xl p-6"
          >
            <h2 className="text-2xl font-semibold text-purple-700 mb-4">預算分配</h2>
            <Pie data={pieData} />
          </motion.div>
          
          <motion.div
            initial={{ opacity: 0, x: 50 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.5, delay: 0.4 }}
            className="bg-white rounded-lg shadow-xl p-6"
          >
            <h2 className="text-2xl font-semibold text-purple-700 mb-4">季度預算使用情況</h2>
            <Bar data={barData} />
          </motion.div>
        </div>
        
        <motion.div
          initial={{ opacity: 0, y: 50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5, delay: 0.6 }}
          className="mt-12 bg-white rounded-lg shadow-xl p-6"
        >
          <h2 className="text-2xl font-semibold text-purple-700 mb-4">預算明細</h2>
          <table className="w-full">
            <thead>
              <tr className="bg-purple-100">
                <th className="p-2 text-left">項目</th>
                <th className="p-2 text-right">金額 (NTD)</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td className="p-2">研究經費</td>
                <td className="p-2 text-right">400,000</td>
              </tr>
              <tr>
                <td className="p-2">活動經費</td>
                <td className="p-2 text-right">300,000</td>
              </tr>
              <tr>
                <td className="p-2">行政開支</td>
                <td className="p-2 text-right">200,000</td>
              </tr>
              <tr>
                <td className="p-2">設備採購</td>
                <td className="p-2 text-right">100,000</td>
              </tr>
              <tr className="font-bold">
                <td className="p-2">總計</td>
                <td className="p-2 text-right">1,000,000</td>
              </tr>
            </tbody>
          </table>
        </motion.div>
      </div>
    </div>
  );
};
export default BudgetPage;