// src/pages/ResourcesPage.js
import React from 'react';
import { motion } from 'framer-motion';
import { ExternalLink } from 'lucide-react';
import { researchReports, events, resources } from '../data/siteData';

const ResourcesPage = () => {
  return (
    <motion.div 
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
      className="bg-gray-100 min-h-screen py-12"
    >
      <div className="container mx-auto px-4">
        <h1 className="text-4xl font-bold text-center text-gray-800 mb-8">人權資源</h1>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
          {resources.map((resource, index) => (
            <motion.div
              key={resource.id}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: index * 0.1 }}
              className="bg-white rounded-lg shadow-xl p-6 flex flex-col"
            >
              <div className="text-purple-600 mb-4">
                <resource.icon size={48} />
              </div>
              <h2 className="text-2xl font-semibold text-gray-800 mb-2">{resource.title}</h2>
              <p className="text-gray-600 mb-4 flex-grow">{resource.description}</p>
              <a 
                href={resource.link} 
                target="_blank" 
                rel="noopener noreferrer" 
                className="inline-flex items-center text-purple-600 hover:text-purple-800 transition duration-300"
              >
                瀏覽資源 <ExternalLink size={16} className="ml-1" />
              </a>
            </motion.div>
          ))}
        </div>
      </div>
    </motion.div>
  );
};

export default ResourcesPage;
