// src/pages/EventsPage.js
import React from 'react';
import { motion } from 'framer-motion';
import { Link } from 'react-router-dom';
import { Calendar, ChevronRight } from 'lucide-react';
import { researchReports, events, resources } from '../data/siteData';


const EventsPage = () => {
  return (
    <motion.div 
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
      className="bg-gray-100 min-h-screen py-12"
    >
      <div className="container mx-auto px-4">
        <h1 className="text-4xl font-bold text-center text-gray-800 mb-8">活動</h1>
        <div className="space-y-8">
          {events.map((event, index) => (
            <motion.div
              key={event.id}
              initial={{ opacity: 0, x: index % 2 === 0 ? -20 : 20 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ duration: 0.5, delay: index * 0.1 }}
              className="bg-white rounded-lg shadow-xl overflow-hidden flex flex-col md:flex-row"
            >
              <div className="md:w-1/3">
                <img src={event.image} alt={event.title} className="w-full h-full object-cover" />
              </div>
              <div className="md:w-2/3 p-6">
                <h2 className="text-2xl font-semibold text-gray-800 mb-2">{event.title}</h2>
                <p className="text-gray-600 mb-4">{event.description}</p>
                <div className="flex items-center text-gray-500 mb-4">
                  <Calendar size={16} className="mr-2" />
                  <span>{event.date}</span>
                </div>
                <Link to={`/events/${event.id}`} className="inline-flex items-center text-purple-600 hover:text-purple-800 transition duration-300">
                  了解更多 <ChevronRight size={16} className="ml-1" />
                </Link>
              </div>
            </motion.div>
          ))}
        </div>
      </div>
    </motion.div>
  );
};

export default EventsPage;