import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Link } from 'react-router-dom';
import { Users, BookOpen, Globe, Sparkles, Mail, ArrowRight, Upload, CheckCircle } from 'lucide-react';

const JoinUsPage = ({ theme }) => {
  const [selectedOpportunity, setSelectedOpportunity] = useState(null);
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    resume: null,
    motivationLetter: '',
  });
  const [uploadStatus, setUploadStatus] = useState('idle');
  const [applicationStatus, setApplicationStatus] = useState('idle');

  const handleChange = (e) => {
    const { name, value, files } = e.target;
    setFormData(prevState => ({
      ...prevState,
      [name]: files ? files[0] : value
    }));
  };

  const handleFileUpload = (e) => {
    const file = e.target.files[0];
    if (file && file.type === 'application/pdf' && file.size <= 5 * 1024 * 1024) {
      setFormData(prevState => ({ ...prevState, resume: file }));
      setUploadStatus('uploaded');
    } else {
      setUploadStatus('error');
    }
  };

  const simulateFileCheck = () => {
    setUploadStatus('checking');
    setTimeout(() => {
      setUploadStatus('success');
    }, 2000);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setApplicationStatus('submitting');
    setTimeout(() => {
      setApplicationStatus('success');
    }, 3000);
  };

  const opportunities = [
    {
      title: "志工招募",
      description: "加入我們的活動團隊，協助組織和執行各項倡議",
      icon: Users,
      requirements: [
        "熱衷於性別平等和人權議題",
        "具備良好的溝通和團隊合作能力",
        "每週能投入至少10小時",
        "具有活動策劃或執行經驗者優先",
        "能夠靈活應對不同類型的任務和挑戰"
      ]
    },
    {
      title: "研究助理",
      description: "參與重要的人權研究項目，累積寶貴的學術經驗",
      icon: BookOpen,
      requirements: [
        "相關領域的學士學位（性別研究、社會學、人權法等）",
        "具備基礎的研究方法和數據分析能力",
        "熟悉學術寫作規範",
        "能夠獨立工作並管理多個項目",
        "有發表相關論文或參與研究項目經驗者優先"
      ]
    },
    {
      title: "社群媒體大使",
      description: "幫助我們在線上擴大影響力，分享重要的人權資訊",
      icon: Globe,
      requirements: [
        "熟悉各大社交媒體平台的運作和最新趨勢",
        "具備出色的文字和視覺內容創作能力",
        "對性別平等和人權議題有深入了解",
        "能夠策劃和執行社交媒體活動",
        "有公關或新聞背景者優先"
      ]
    },
    {
      title: "校園人權觀察員",
      description: "成為我們的眼睛和耳朵，報告和記錄校園內的人權議題",
      icon: Sparkles,
      requirements: [
        "現役大學生或研究生",
        "對校園人權議題有敏銳的觀察力",
        "具備基本的調查和報告撰寫能力",
        "能夠保持客觀中立的態度",
        "修過相關人權或法律課程者優先"
      ]
    }
  ];

  return (
    <div className={`min-h-screen py-12 ${theme === 'dark' ? 'bg-gray-900 text-white' : 'bg-purple-50 text-gray-900'}`}>
      <div className="container mx-auto px-4">
        <motion.h1 
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          className={`text-4xl font-bold text-center mb-12 ${theme === 'dark' ? 'text-purple-300' : 'text-purple-800'}`}
        >
          加入我們，共創平等未來
        </motion.h1>

        <section className="mb-16">
          <h2 className={`text-3xl font-bold mb-8 text-center ${theme === 'dark' ? 'text-white' : 'text-gray-800'}`}>
            選擇您感興趣的職位
          </h2>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
            {opportunities.map((opportunity, index) => (
              <motion.div
                key={index}
                whileHover={{ scale: 1.05 }}
                className={`p-6 rounded-lg shadow-lg cursor-pointer ${
                  theme === 'dark' ? 'bg-gray-800 hover:bg-gray-700' : 'bg-white hover:bg-purple-100'
                } ${selectedOpportunity === index ? 'ring-2 ring-purple-500' : ''}`}
                onClick={() => setSelectedOpportunity(index)}
              >
                <div className="flex items-start">
                  <opportunity.icon className={`mr-4 ${theme === 'dark' ? 'text-purple-400' : 'text-purple-600'}`} size={24} />
                  <div>
                    <h3 className={`text-xl font-semibold mb-2 ${theme === 'dark' ? 'text-white' : 'text-gray-800'}`}>{opportunity.title}</h3>
                    <p className={`${theme === 'dark' ? 'text-gray-300' : 'text-gray-600'}`}>{opportunity.description}</p>
                    {selectedOpportunity === index && (
                      <motion.div
                        initial={{ opacity: 0, height: 0 }}
                        animate={{ opacity: 1, height: 'auto' }}
                        exit={{ opacity: 0, height: 0 }}
                        className="mt-4"
                      >
                        <h4 className={`font-semibold mb-2 ${theme === 'dark' ? 'text-white' : 'text-gray-800'}`}>職位要求：</h4>
                        <ul className={`list-disc list-inside ${theme === 'dark' ? 'text-gray-300' : 'text-gray-600'}`}>
                          {opportunity.requirements.map((req, idx) => (
                            <li key={idx} className="mb-1">{req}</li>
                          ))}
                        </ul>
                      </motion.div>
                    )}
                  </div>
                </div>
              </motion.div>
            ))}
          </div>
        </section>

        <AnimatePresence>
          {selectedOpportunity !== null && (
            <motion.section
              initial={{ opacity: 0, y: 50 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: 50 }}
              className="mb-16"
            >
              <h2 className={`text-3xl font-bold mb-8 text-center ${theme === 'dark' ? 'text-white' : 'text-gray-800'}`}>
                申請表格
              </h2>
              <form onSubmit={handleSubmit} className={`p-6 rounded-lg shadow-lg ${theme === 'dark' ? 'bg-gray-800' : 'bg-white'}`}>
                <div className="mb-4">
                  <label htmlFor="name" className={`block mb-2 font-semibold ${theme === 'dark' ? 'text-white' : 'text-gray-700'}`}>姓名</label>
                  <input
                    type="text"
                    id="name"
                    name="name"
                    value={formData.name}
                    onChange={handleChange}
                    className={`w-full p-2 border rounded ${theme === 'dark' ? 'bg-gray-700 border-gray-600 text-white' : 'bg-white border-gray-300'}`}
                    required
                  />
                </div>
                <div className="mb-4">
                  <label htmlFor="email" className={`block mb-2 font-semibold ${theme === 'dark' ? 'text-white' : 'text-gray-700'}`}>電子郵件</label>
                  <input
                    type="email"
                    id="email"
                    name="email"
                    value={formData.email}
                    onChange={handleChange}
                    className={`w-full p-2 border rounded ${theme === 'dark' ? 'bg-gray-700 border-gray-600 text-white' : 'bg-white border-gray-300'}`}
                    required
                  />
                </div>
                <div className="mb-4">
                  <label className={`block mb-2 font-semibold ${theme === 'dark' ? 'text-white' : 'text-gray-700'}`}>簡歷（PDF格式，最大5MB）</label>
                  <div className={`p-4 border-2 border-dashed rounded-lg text-center ${theme === 'dark' ? 'bg-gray-700 border-gray-600' : 'bg-white border-gray-300'}`}>
                    <input
                      type="file"
                      accept=".pdf"
                      onChange={handleFileUpload}
                      className="hidden"
                      id="resume-upload"
                    />
                    <label htmlFor="resume-upload" className="cursor-pointer">
                      <Upload className={`mx-auto mb-2 ${theme === 'dark' ? 'text-purple-400' : 'text-purple-600'}`} />
                      <p>{formData.resume ? formData.resume.name : '點擊或拖放文件到此處上傳'}</p>
                    </label>
                  </div>
                  {uploadStatus === 'uploaded' && (
                    <motion.button
                      initial={{ opacity: 0 }}
                      animate={{ opacity: 1 }}
                      className={`mt-2 px-4 py-2 rounded ${theme === 'dark' ? 'bg-purple-600 text-white' : 'bg-purple-100 text-purple-700'}`}
                      onClick={simulateFileCheck}
                    >
                      檢查文件
                    </motion.button>
                  )}
                  {uploadStatus === 'checking' && (
                    <p className={`mt-2 ${theme === 'dark' ? 'text-yellow-300' : 'text-yellow-600'}`}>正在檢查文件...</p>
                  )}
                  {uploadStatus === 'success' && (
                    <p className={`mt-2 ${theme === 'dark' ? 'text-green-300' : 'text-green-600'}`}>文件檢查通過！</p>
                  )}
                  {uploadStatus === 'error' && (
                    <p className={`mt-2 ${theme === 'dark' ? 'text-red-300' : 'text-red-600'}`}>文件檢查失敗，請確保文件為PDF格式且不超過5MB。</p>
                  )}
                </div>
                <div className="mb-4">
                  <label htmlFor="motivationLetter" className={`block mb-2 font-semibold ${theme === 'dark' ? 'text-white' : 'text-gray-700'}`}>動機信</label>
                  <textarea
                    id="motivationLetter"
                    name="motivationLetter"
                    value={formData.motivationLetter}
                    onChange={handleChange}
                    rows="5"
                    className={`w-full p-2 border rounded ${theme === 'dark' ? 'bg-gray-700 border-gray-600 text-white' : 'bg-white border-gray-300'}`}
                    required
                  ></textarea>
                </div>
                <div className="text-center">
                  <button
                    type="submit"
                    className={`bg-purple-600 text-white px-6 py-2 rounded-full font-semibold hover:bg-purple-700 transition duration-300 ${applicationStatus !== 'idle' ? 'opacity-50 cursor-not-allowed' : ''}`}
                    disabled={applicationStatus !== 'idle'}
                  >
                    {applicationStatus === 'idle' ? '提交申請' : '處理中...'}
                  </button>
                </div>
              </form>
            </motion.section>
          )}
        </AnimatePresence>

        // ... 前面的代碼保持不變

        <AnimatePresence>
          {applicationStatus === 'success' && (
            <motion.div
              initial={{ opacity: 0, scale: 0.8 }}
              animate={{ opacity: 1, scale: 1 }}
              exit={{ opacity: 0, scale: 0.8 }}
              className={`fixed inset-0 flex items-center justify-center z-50 ${theme === 'dark' ? 'bg-gray-900 bg-opacity-75' : 'bg-gray-100 bg-opacity-75'}`}
            >
              <div className={`p-8 rounded-lg shadow-xl ${theme === 'dark' ? 'bg-gray-800' : 'bg-white'}`}>
                <CheckCircle className="text-green-500 w-16 h-16 mx-auto mb-4" />
                <h3 className={`text-2xl font-bold mb-4 text-center ${theme === 'dark' ? 'text-white' : 'text-gray-800'}`}>申請成功！</h3>
                <p className={`text-center mb-6 ${theme === 'dark' ? 'text-gray-300' : 'text-gray-600'}`}>
                  您的申請已成功提交。請查看您的電子郵件以獲取驗證連結和後續指示。
                </p>
                <div className="text-center">
                  <button
                    onClick={() => {
                      setApplicationStatus('idle');
                      setSelectedOpportunity(null);
                      setFormData({name: '', email: '', resume: null, motivationLetter: ''});
                    }}
                    className={`bg-purple-600 text-white px-6 py-2 rounded-full font-semibold hover:bg-purple-700 transition duration-300`}
                  >
                    返回首頁
                  </button>
                </div>
              </div>
            </motion.div>
          )}
        </AnimatePresence>

        <section className="mt-16 text-center">
          <h2 className={`text-3xl font-bold mb-8 ${theme === 'dark' ? 'text-white' : 'text-gray-800'}`}>
            有疑問嗎？
          </h2>
          <p className={`mb-8 ${theme === 'dark' ? 'text-gray-300' : 'text-gray-600'}`}>
            如果您對參與機會或申請過程有任何疑問，請隨時與我們聯繫。
          </p>
          <Link
            to="/contact"
            className={`bg-purple-600 text-white px-8 py-3 rounded-full font-semibold text-lg shadow-lg hover:bg-purple-700 transition duration-300 inline-flex items-center ${theme === 'dark' ? 'hover:bg-purple-500' : 'hover:bg-purple-700'}`}
          >
            聯絡我們 <Mail className="ml-2" />
          </Link>
        </section>
      </div>
    </div>
  );
};

export default JoinUsPage;